<template>
  <div class="asd">
    <div v-for="item in msg">
    	{{item}}
    </div>
    <router-link to="/qwe">go to qwe</router-link>
  </div>
</template>

<script>
export default {
  name: 'asd',
  data () {
    return {
    	myTitle:"asd-title",
      msg: [
      	"111",
      	"222",
      	"333"
      ]
    }
  },
  metaInfo: {
    title: this.myTitle,
    titleTemplate: '%s - by vue-meta asd',
    meta:[
    	{name: 'viewport ', content: 'initial-scale=1.0,user-scalable=no,maximum-scale=1,width=device-width'}
    ]
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



